import React, { Component } from 'react';
import { Select } from 'element-react';
import './index.scss';
import Codemirror from 'react-codemirror';
import { connect } from 'react-redux';
import { fromJS, is } from 'immutable';

class Xselect extends Component {
	state = {
		options: [{
			value: '选项1',
			label: '黄金糕'
		}, {
			value: '选项2',
			label: '双皮奶',
			disabled: true
		}, {
			value: '选项3',
			label: '蚵仔煎'
		}, {
			value: '选项4',
			label: '龙须面'
		}, {
			value: '选项5',
			label: '北京烤鸭'
		}],
		value: ''
	}

	componentDidMount() {
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}

	onChange = (n) => {
		console.log(n)
		this.setState({
			value: n
		})
	}

	render() {
		const { defaultOptions} = this.props.configs

		return (
			<div id="x-select">
				<h3>Select 选择器</h3>
				<div className="mt-20 mb-20">
					<Select value={this.state.value} placeholder="请选择" onChange={this.onChange}>
						{
							this.state.options.map(el => {
								return <Select.Option key={el.value} label={el.label} value={el.value} disabled={el.disabled}/>
							})
						}
					</Select>
				</div>
				<Codemirror value={
					`<Select value={this.state.value} placeholder="请选择" onChange={this.onChange}>
	{
		this.state.options.map(el => {
			return <Select.Option key={el.value} label={el.label} value={el.value} disabled={el.disabled}/>
		})
	}
</Select>`
				} options={defaultOptions} />

			</div>
		);
	}
}

export default connect(state => ({
	configs: state.configs
}), {

})(Xselect)
